body {
  max-width: 960px;
  margin: auto;
  padding-left: .9em;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 12px;
}

h1 {
  letter-spacing: .1em;
  padding-top: 1em;
  padding-bottom: 1em;
  font-size: 3em;
}

h2 {
  padding-bottom: 1em;
  font-size: 1.5em;
}

h3 {
  padding-bottom: 1em;
  font-size: 1em;
}

main {
  grid-template-columns: 40% 60%;
  margin-top: 3em;
  display: grid;
}

header {
  text-align: center;
  margin: auto 2em;
}

section {
  margin: auto 1em 4em 2em;
}

i {
  margin-right: .5em;
}

p {
  margin: .2em auto;
}

hr {
  background-color: #d3d3d3;
  border: none;
  height: 1px;
}

h1, h2, h3 {
  margin-bottom: 0;
  font-weight: 100;
}

#mainLeft {
  border-right: 1px solid #d3d3d3;
}
